.input-form {
  width: 100%;
  text-align: left;
  margin-top: 1.5rem;
}
.input-form__header {
  display: grid;
  grid-template-columns: 35% 65%;
  margin-bottom: 0.5rem;
  font-size: 0.7rem;
}
.input-form__header-title {
  font-weight: bold;
}
.input-form__header-validation-message {
  color: red;
}