.card {
  width: 15rem;
  height: 8rem;
  margin: 1rem;
  margin-bottom: 3rem;
  padding: 1.6rem;
  border-radius: 1rem;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.25);
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  color: #525252;
}

.card-header {
  flex-grow: 1;
}
.card-header__text {
  font-weight: bold;
}

.card-body {
  margin-top: 1rem;
  flex-grow: 2;
}
.card-body__ic-chip {
  width: 3rem;
  height: 2rem;
  margin-bottom: 1rem;
  border-radius: 0.3rem;
  background-color: burlywood;
}
.card-body__number {
  margin-bottom: 1rem;
}

.card-footer {
  width: 100%;
  flex-grow: 1;
  display: grid;
  grid-template-columns: 3fr 3rem;
  text-align: left;
}
.card-footer__name {
  grid-column-start: 1;
  grid-column-end: span 1;
}
.card-footer__expiration-date {
  grid-column-start: 2;
  grid-column-end: span 1;
}